<template>
  <div class="ProfessionalQualificationTotalBackground">
    <!--     <input
                  type="text"
                  class="ProfessionalQualificationUserNumberInput"
                  v-model="ProfessionalQualificationUserNumber"
                  placeholder="请输入学生学号"
                />
                <button  class="ProfessionalQualificationSeekButton" @click="ProfessionalQualificationSeekButtonClick">查询</button>
                <button  class="ProfessionalQualificationEditButton" @click="ProfessionalQualificationEditButtonClick">编辑</button>
             -->
    <div class="ProfessionalQualificationTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 3%">姓名</th>
            <th style="width: 4%">荣誉证书名</th>
            <th style="width: 4%">荣誉证书编号</th>
            <th style="width: 4%">获得时间</th>
            <th style="width: 4%">颁发部门</th>
            <th style="width: 4%">资格证书级别</th>
            <th style="width: 5%">照片</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(studentRecord, index) in ProfessionalQualificationTable"
            :key="index"
          >
            <td>
              {{ studentRecord.professionalQualificationsList[0].studentName }}
            </td>
            <td>
              {{
                studentRecord.professionalQualificationsList[0].credentialsName
              }}
            </td>
            <td>
              {{
                studentRecord.professionalQualificationsList[0]
                  .certificateNumber
              }}
            </td>

            <td>
              {{
                formatDate(
                  studentRecord.professionalQualificationsList[0].obtainingTime
                )
              }}
            </td>
            <td>
              {{
                studentRecord.professionalQualificationsList[0]
                  .issuingDepartment
              }}
            </td>
            <td>
              {{
                studentRecord.professionalQualificationsList[0].classification
              }}
            </td>
            <td
              @click="
                pictureInspectClick(
                  studentRecord.professionalQualificationsList[0].photo
                )
              "
            >
              <img
                :src="studentRecord.professionalQualificationsList[0].photo"
                alt="获奖照片"
                width="100"
                height="30"
              />
            </td>
            <!-- 查看详情 -->
            <td @click="ViewDetails(studentRecord)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="ProfessionalQualificationPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>

    <div v-if="ifDetails" class="ProfessionalQualificationDetailsBackground">
      <div class="ProfessionalQualificationDetailsFormBackground">
        <span class="ProfessionalQualificationDetailsFormTitle"
          >学生奖项详细信息</span
        >
        <form class="ProfessionalQualificationDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div class="label-row">
            <label>身份证号 : {{ studentDetails[0].idCard }}</label>
          </div>
          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>准考证号 : {{ data.examinationCertificateNumber }}</label>
            </div>
            <div class="label-row">
              <label>荣誉证书名 : {{ data.credentialsName }}</label>
            </div>
            <div class="label-row">
              <label>荣誉证书编号 : {{ data.certificateNumber }}</label>
            </div>
            <div class="label-row">
              <label>获得时间 : {{ data.obtainingTime }}</label>
            </div>
            <div class="label-row">
              <label>颁发部门 : {{ data.issuingDepartment }}</label>
            </div>
            <div class="label-row">
              <label>资格证书级别 : {{ data.classification }}</label>
            </div>
            <div class="label-row">
              <label>照片 : </label>
              <label
                ><img :src="data.photo" alt="图片描述" width="400" height="300"
              /></label>
            </div>
            <br />
          </div>
        </form>
        <button
          class="ProfessionalQualificationDetailsFormCloseButton"
          @click="ProfessionalQualificationDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>

    <div
      v-if="ifPicture"
      class="ProfessionalQualificationDetailsBackground"
      @click="ProfessionalQualificationDetailsBackgroundClick"
    >
      <img :src="this.pictureURL" alt="图片描述" width="70%" height="300" />
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      ProfessionalQualificationTable: [],
      ifDetails: false,
      ifPicture: false,
      pictureURL: "",
      studentDetails: {},
      currentPage: 1,
      pageSize: 13,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    formatDate(date) {
      const d = new Date(date);
      return `${d.getFullYear()}-${(d.getMonth() + 1)
        .toString()
        .padStart(2, "0")}-${d.getDate().toString().padStart(2, "0")}`;
    },
    ProfessionalQualificationDetailsBackgroundClick() {
      this.ifPicture = false;
    },
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    pictureInspectClick(picture) {
      this.pictureURL = picture;
      this.ifPicture = true;
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getProfessionalQualifications",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.ProfessionalQualificationTable =
            informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },

    RenewLabel(studentRecord) {
      this.studentDetails = studentRecord.professionalQualificationsList;
    },
    ProfessionalQualificationDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.ProfessionalQualificationPagination {
  position: absolute;
  top: 95%;
}
.ProfessionalQualificationUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.ProfessionalQualificationTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.ProfessionalQualificationTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/ProfessionalQualificationForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
